                      label="Aspect Height"
                      v-model="imageHeight"
                      :disabled="generating ? true : false"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-list-item>

              <v-list-item>
                <div class="text-h6">Advanced Settings</div>
                <v-divider class="my-2 pb-2"></v-divider>
              </v-list-item>

              <v-list-item>
                <v-text-field
                  label="Negative Prompt"
                  v-model="negativePrompt"
                  :disabled="generating ? true : false"
                ></v-text-field>
              </v-list-item>

              <v-list-item>
