<script>
import GeneratedImage from "./GeneratedImage.vue";

export default {
  name: "ImageEditor",
  components: {
    GeneratedImage,
  },
  data() {
    return {
      dialogue: [
        "Sending Request to Midjourney",
        "Propogating the swoof...",
        "Prepping seed...",
        "Loading core libraries...",
        "Fetching latest user data...",
        "Loading...",
      ],
      currentDialogue: "Initializing...",
      chaosValue: 0,
      weirdValue: 0,
      nijiVersion: "default",
      midjourneyVersion: "5.2",
      generating: false,
      progress: 0,
      generatedImageUrl: null,
      generatedImageUrls: null,
      selectedModel: 1,
      imagePrompt: "",
      negativePrompt: "",
      imageWidth: 16,
      imageHeight: 9,
      valid: false,
      headers: {
        Authorization: "Bearer 558ee60b-af70-42a5-b27a-46215f74d0d2",
        "Content-Type": "application/json",
      },
    };
  },
  methods: {
    updateLoadingDialogue() {
      for (const dialogue of this.dialogue) {
        setTimeout(() => {
          this.currentDialogue = dialogue;
        }, Math.floor(Math.random() * (20000 - 5000 + 1)) + 5000);
      }
    },
    createPrompt() {
      this.currentDialogue = "Initializing...";
      this.updateLoadingDialogue();
      let prompt =
        this.imagePrompt +
        " --ar " +
        this.aspectRatioReq +
        " --c " +
        this.chaosValue +
        " --w " +
        this.weirdValue;

      // Check for model version
      if (this.selectedModel === 1) {
        prompt += " --v " + this.midjourneyVersion;
      } else if (this.selectedModel === 2) {
        if (this.nijiVersion !== "default") {
          prompt += " --niji 5 --style " + this.nijiVersion;
        } else {
          prompt += " --niji 5 ";
        }
      }

      if (this.negativePrompt !== "") {
        prompt += " --no " + this.negativePrompt;
      }

      return prompt;
    },
    async generateImage() {
      this.generatedImageUrls = null;
      this.generatedImageUrl = null;
      this.generating = true;
      this.progress = 0;
      // Createrequest data for API
      const reqData = JSON.stringify({
        msg: this.createPrompt(),
        ref: "",
        webhookOverride: "",
        ignorePrefilter: "false",
      });

      console.log(this.createPrompt());

      try {
        const { data } = await this.$http.post(
          "https://api.thenextleg.io/v2/imagine",
          reqData,
          { headers: this.headers }
        );
        console.log("Request Sent => ", data.messageId);

        // After post check updates of prompt generation
        this.getImageStatus(data.messageId);
      } catch (error) {
        console.error("ERROR AT PROMPT => ", error);
      }
    },
    async getImageStatus(messageId) {
      try {
        // Store progress percentile sent back from api
        console.log("Starting job for", messageId);
        // Setting interval to run get request every n seconds -> add config file
        const statusInterval = setInterval(async () => {
          // Check if generation is complete
          if (this.progress !== 100) {
            console.log("The progress is below 100");
            try {
              const response = await this.$http.get(
                `https://api.thenextleg.io/v2/message/${messageId}?expireMins=2`,
                { headers: this.headers }
              );
              console.log("Progress Recieved for", messageId, " =>", response);

              // Update progress variable every time request is run
              this.progress = response.data.progress;

              if (response.data.progressImageUrl) {
                console.log(response.data.progressImageUrl);
                this.generatedImageUrl = response.data.progressImageUrl;
              }

              // If proress in 100% stop sending requests
              if (response.data.progress === 100) {
                console.log("The progress is above 100");
                console.log(
                  "Progress Complete for",
                  messageId,
                  "! =>",
                  response.data.response.imageUrl
                );
                this.generating = false;
                this.generatedImageUrl = response.data.response.imageUrl;
                this.generatedImageUrls = response.data.response.imageUrls;
                console.log("Generated img URLS => ", this.generatedImageUrls);
                clearInterval(statusInterval);
              }
            } catch (error) {
              console.error("ERROR AT STATUS CHECK =>", error);
            }
          }
        }, 2000);
      } catch (error) {
        console.error(error);
      }
    },
    emitImagePrompt() {
      const imageData = {
        prompt: this.imagePrompt,
      };
      this.$emit("submitImagePrompt", imageData);
    },
  },
  computed: {
    aspectRatio() {
      return this.imageWidth + "/" + this.imageHeight;
    },
    aspectRatioReq() {
      return this.imageWidth + ":" + this.imageHeight;
    },
  },
};
</script>

